body,
html {
  height: 100%;
}

#tsparticles canvas {
  display: block;
  vertical-align: bottom;
  transform: scale(1);
  opacity: 1;
  transition: opacity 0.8s ease, transform 1.4s ease;
}

#tsparticles {
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: -10;
  top: 0;
  left: 0;
  border-radius: 10%;
}

#tsparticles2 {
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: -15;
  top: 0;
  left: 0;
}

#heading {
  color: white;
  z-index: 10;
}

.login-form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  background-color: #000000c0;
  padding: 5%;
  border-radius: 10%;
  box-shadow: rgba(255, 255, 255, 0.25) 0px 54px 55px,
    rgba(255, 255, 255, 0.12) 0px -12px 30px,
    rgba(255, 255, 255, 0.12) 0px 4px 6px,
    rgba(255, 255, 255, 0.17) 0px 12px 13px,
    rgba(255, 255, 255, 0.09) 0px -3px 5px;
}

#textfield {
  box-shadow: rgba(3, 102, 214, 0.3) 0px 0px 0px 3px;
  border-radius: 5%;
  margin-top: 5%;
  margin-bottom: 5%;
  color: white;
}

#textfield input {
  color: white;
}

.heading-container {
  text-align: center;
  width: 100%;
  top: 15%;
}

* {
  margin: 0;
  padding: 0;
}
.title h1 {
  font-size: 36px;
  font-family: "Lato", sans-serif;
  margin-top: 10em;
  margin-left: auto;
  margin-right: auto;
  text-align: center;

  /* Stryling the text and giving it gradiant */
  color: #ffffff;
  background: -webkit-linear-gradient(#eee, #333);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  text-shadow: 6px 6px 0px rgba(0, 0, 0, 0.2);

  /* Make sure text is infront of background images */
  display: block;
  position: relative;
  z-index: 3;
}

@keyframes move-twink-back {
  from {
    background-position: 0 0;
  }
  to {
    background-position: -10000px 5000px;
  }
}

@keyframes move-clouds-back {
  from {
    background-position: 0 0;
  }
  to {
    background-position: 10000px 0;
  }
}

.stars,
.twinkling,
.clouds {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  display: block;
}

.stars {
  background: #000 url(/static/media/stars.90c266f7.png) repeat top center;
  z-index: 1;
}

.twinkling {
  background: transparent url(data:image/png;base64,) repeat top center;
  z-index: -1;
  animation: move-twink-back 200s linear infinite;
}

.clouds {
  background: transparent url(/static/media/clouds.655c9c06.png) repeat top center;
  z-index: -1;
  opacity: 0.4;
  animation: move-clouds-back 200s linear infinite;
}

label {
  color: white;
}

.btn {
  text-decoration: none;
  border: 1px solid rgb(146, 148, 248);
  position: relative;
  overflow: hidden;
  background: black;
  opacity: 0.6;
  height: 60px;
  color: white;
}

.btn:hover {
  box-shadow: 1px 1px 25px 10px rgba(146, 148, 248, 0.4);
}

.btn:before {
  content: "";
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    120deg,
    transparent,
    rgba(146, 148, 248, 0.4),
    transparent
  );
  transition: all 650ms;
}

.btn:hover:before {
  left: 100%;
}

a {
  font-size: 1.5rem;
  padding-top: 1rem;
  padding-bottom: 1rem;
  color: #f4f4f4;
  text-align: center;
  text-transform: uppercase;
}

.anim {
  border-radius: 10%;
}

#center {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: 20;
}

.textfield__label {
  color: white !important;
  font-family: Montserrat;
}

#errortext{
  color: red;
  font-family: Montserrat;
  text-align: center;
  padding: 5%;
  width: 250px;
