* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
#content-container {
  display: flex;
  flex-direction : column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100vw;
  font-family: "Raleway", sans-serif;
  font-weight: bold;
}


a {
  position: relative;
  display: inline-block;
  padding: 25px 30px;
  margin: 40px 0;
  color: #03e9f4;
  text-decoration: none;
  text-transform: uppercase;
  transition: 0.5s;
  letter-spacing: 4px;
  overflow: hidden;
  margin-right: 50px;
}
a:hover {
  background: #03e9f4;
  color: #050801;
  box-shadow: 0 0 5px #03e9f4, 0 0 25px #03e9f4, 0 0 50px #03e9f4,
    0 0 200px #03e9f4;
  -webkit-box-reflect: below 1px linear-gradient(transparent, #0005);
}
.down {
  margin-left: 15%;
  -webkit-filter: hue-rotate(270deg);
          filter: hue-rotate(270deg);
}
.up {
  margin-left: 15%;
  -webkit-filter: hue-rotate(110deg);
          filter: hue-rotate(110deg);
}

.left {
  margin-left: 15%;
  -webkit-filter: hue-rotate(190deg);
          filter: hue-rotate(190deg);
}
.right{
  margin-left: 15%;
}
a span {
  position: absolute;
  display: block;
}
a span:nth-child(1) {
  top: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background: linear-gradient(90deg, transparent, #03e9f4);
  animation: animate1 1s linear infinite;
}
@keyframes animate1 {
  0% {
    left: -100%;
  }
  50%,
  100% {
    left: 100%;
  }
}
a span:nth-child(2) {
  top: -100%;
  right: 0;
  width: 2px;
  height: 100%;
  background: linear-gradient(180deg, transparent, #03e9f4);
  animation: animate2 1s linear infinite;
  animation-delay: 0.25s;
}
@keyframes animate2 {
  0% {
    top: -100%;
  }
  50%,
  100% {
    top: 100%;
  }
}
a span:nth-child(3) {
  bottom: 0;
  right: 0;
  width: 100%;
  height: 2px;
  background: linear-gradient(270deg, transparent, #03e9f4);
  animation: animate3 1s linear infinite;
  animation-delay: 0.5s;
}
@keyframes animate3 {
  0% {
    right: -100%;
  }
  50%,
  100% {
    right: 100%;
  }
}

a span:nth-child(4) {
  bottom: -100%;
  left: 0;
  width: 2px;
  height: 100%;
  background: linear-gradient(360deg, transparent, #03e9f4);
  animation: animate4 1s linear infinite;
  animation-delay: 0.75s;
}
@keyframes animate4 {
  0% {
    bottom: -100%;
  }
  50%,
  100% {
    bottom: 100%;
  }
}

#maze-container {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

#maze {
  display: inline-block;
  position: relative;
  background-color: #a7c53f;
  background-image: radial-gradient(
      circle at 0% 0%,
      transparent 50%,
      rgba(0, 0, 0, 0.1) 50%
    ),
    radial-gradient(circle at center, rgba(0, 0, 0, 0.1) 50%, transparent 50%),
    radial-gradient(
      circle at 100% 100%,
      transparent 50%,
      rgba(0, 0, 0, 0.1) 50%
    ),
    radial-gradient(circle at 0% 100%, transparent 50%, rgba(0, 0, 0, 0.1) 50%),
    radial-gradient(circle at 100% 0%, transparent 50%, rgba(0, 0, 0, 0.1) 50%);
  background-size: 8em 8em;
  border: 2px solid black;
  align-self: center;
  box-shadow: rgba(255, 255, 255, 0.25) 0px 34px 35px,
    rgba(255, 255, 255, 0.12) 0px -12px 30px,
    rgba(255, 255, 255, 0.12) 0px 4px 6px,
    rgba(255, 255, 255, 0.17) 0px 12px 13px,
    rgba(255, 255, 255, 0.09) 0px -9px 9px;
}
#maze div {
  display: flex;
}
#maze div div {
  position: relative;
  width: 1.5em;
  height: 1.5em;
}

#maze div.hero::after {
  content: "\1F6B6" !important;
  font-size: 1.5em;
}

#maze div.key::after {
  content: "\1F511" !important;
  font-size: 1em;
  color: gold;
}

#maze div.goal::after {
  content: "\1F6AA" !important;
  font-size: 1em;
  color: gold;
}

#maze div div.wall {
  background-color: #454545;
  background-image: linear-gradient(
      45deg,
      rgba(0, 0, 0, 0.2) 45%,
      transparent 55%
    ),
    linear-gradient(to bottom, rgba(0, 0, 0, 0.2) 45%, transparent 55%);
  background-size: 0.5em 0.5em;
}

.buttoncon {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

#maze div div.invisible {
  background-color: #000000;
  background-size: 0.5em 0.5em;
}

